@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700,900&display=swap&subset=cyrillic);
/**
 * Font
 */

::-moz-selection {
  background: rgba(0, 0, 251, 0.99);
  color: #ffffff;
}

::selection {
  background: rgba(0, 0, 251, 0.99);
  color: #ffffff;
}

/**
 * Normalize
 */
body {
  background: #fff;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 15px;
}

a:hover,
a:focus {
  color: #0000fb;
}

.title {
  margin-bottom: 0;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 8px;
}

h4.title {
  font-size: 16px;
}

h4.title::before {
  content: "/";
}

p {
  font-weight: normal;
  letter-spacing: 1px;
  line-height: 1.5;
}

.hide {
  display: none;
}

/* Text meant only for screen readers. */
/* todo test with screen reader */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  -webkit-clip-path: none;
          clip-path: none;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/**
 * Entry header
 */
.entry-header {
  overflow-x: auto;
  overflow-y: hidden;
  width: 50%;
  border: 2px solid #ff381d;
  margin-bottom: 50px;
}

@media (max-width: 576px) {
  .entry-header {
    width: 95%;
  }
}
.entry-header .entry-title {
  margin-left: 17px;
  margin-bottom: -0.1rem;
  margin-right: -1px;
  padding-left: 10px;
  padding-bottom: 0px;
  color: white;
  background-color: #ff381d;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 6px;
  font-size: 24px;
  width: calc(100% - 17px);
}

/**
 * Entry content
 */

.entry-content p > a {
  color: #ff381d;
  text-decoration: underline;
}

.entry-content p > a:hover,
.entry-content p > a:focus {
  text-decoration: none;
}

/**
 * Buttons
 */

.btn-outline-primary {
  color: #ff381d;
  border-color: #ff381d;
}

.btn {
  position: relative;
  padding-right: 15%;
  border-radius: 0;
  letter-spacing: 8px;
  font-weight: bold;
  text-decoration: none !important;
  z-index: 2;
}

.btn-outline-primary:not(:disabled):not(.disabled).active,
.btn-outline-primary:not(:disabled):not(.disabled):active,
.show > .btn-outline-primary.dropdown-toggle {
  color: #fff;
  background-color: #ff381d;
  border-color: #ff381d;
}

.btn-outline-primary:hover {
  color: #fff;
  background-color: #ff381d;
  border-color: #ff381d;
}

.btn-outline-primary.focus,
.btn-outline-primary:focus {
  box-shadow: 0 0 0 0.2rem #ff381d;
}

/**
 * Site Navigation
 */
.navbar {
  background-color: white;
  z-index: 3;
}

.nav-item {
  text-align: right;
}

.navbar-nav .nav-item .nav-link {
  display: inline-block;
  padding: 8px;
  color: #ff381d;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 2px;
  cursor: pointer;
}

.navbar .nav-link:before {
  content: "/ ";
}

.navbar a.social-media-link {
  display: inline-block;
  color: #ff381d;
  padding: 2px 6px 1px 6px;
  margin: 4px 0px 0px 2px;
  margin-left: 2px;
  width: 15px;
  text-align: center;
  box-sizing: content-box;
  text-decoration: none;
}

.navbar a.social-media-link:nth-child(2) {
  margin-left: 8px;
}

.navbar .nav-item a.social-media-link:hover,
.navbar .nav-item a.social-media-link:focus,
.navbar .nav-item .nav-link:hover,
.navbar .nav-item .nav-link:focus {
  color: #0000fb;
}

.navbar-light .navbar-toggler {
  border: none;
}

.navbar-light .navbar-toggler-icon {
  background-image: url(../../static/media/menu-24px.e001db08.svg);
}

@media (max-width: 768px) {
  .navbar a.social-media-link {
    display: inline;
  }
  .navbar-nav .nav-item .nav-link {
    display: block;
  }
}

/**
 * Home
 */
#home {
  position: relative;
  padding-top: 53px;
  margin-bottom: 56px;
}

.what-is-it-row {
  height: calc((100vh - 89px) * 0.35);
}

.hair-row {
  height: calc((100vh - 89px) * 0.35);
}

.come-row,
.donate-row {
  height: calc((100vh - 89px) * 0.15);
}

.banner-col,
.center-col,
.right-col,
.left-col {
  height: 100%;
}

.center-col,
.right-col,
.left-col {
  border-bottom: 2px solid #ff381d;
}

.hair-row .center-col,
.hair-row .right-col,
.hair-row .left-col,
.mobile-a-row .center-col {
  border-bottom: none;
}

.md-bottom-row .center-col {
  border-bottom: none;
}

.banner-col {
  text-align: right;
}

.banner-img {
  width: auto;
  height: 100%;
}

.what-is-it-row .left-col {
  display: flex;
  align-items: flex-end;
}

.what-is-it {
  color: #ff381d;
  text-transform: uppercase;
  font-size: 2.4rem;
  letter-spacing: 0px;
  margin: 0;
  padding: 0.5rem 1rem 0 0;
  cursor: pointer;
}

.center-col {
  border-right: 2px solid #ff381d;
  border-left: 2px solid #ff381d;
}

.soma-letter-container {
  display: block;
  padding: 10px;
  height: 9vh;
}

.soma-letter {
  height: 100%;
}

.hair-row .left-col {
  text-align: center;
}

.hair-img {
  height: 100%;
  display: inline-block;
  padding-right: 10px;
}

.circle-img,
.stick-img {
  display: block;
}

.circle-img {
  width: 36px;
}

.stick-img {
  width: 90px;
  -webkit-transform: translate(4vw, 3vh);
          transform: translate(4vw, 3vh);
}

.heart-button {
  height: 100%;
  text-align: center;
  padding: 6px;
}

.heart-button img {
  width: auto;
  height: 75%;
  margin-right: 40%;
}

.heart-button-text {
  display: inline-block;
  margin-left: 20%;
  color: #ff381d;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.5rem;
}

.heart-button-text, .what-is-it {
  margin-left: 20%;
}


.heart-button:hover,
.heart-button-text:hover {
  text-decoration: none;
}

.heart-button-come img {
  -webkit-transform: scaleY(-1);
          transform: scaleY(-1);
}

/**
 * Marquee
 */
.marquee {
  color: #ff381d;
  bottom: 0;
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 5px;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}

.marquee .repeat {
  display: inline-block;
  -webkit-animation: marquee 13s linear infinite;
          animation: marquee 13s linear infinite;
}

@-webkit-keyframes marquee {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes marquee {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

/*
 * Site description
 */

.site-description {
  background-color: #5caf9f;
  color: white;
  padding: 20px 40px;
  min-height: calc((100vh - 89px) * 0.35);
}

@media only screen and (max-width: 960px) {
  .what-is-it-row {
    height: calc((100vh - 89px) * 0.3);
  }

  .hair-row {
    height: calc((100vh - 89px) * 0.33);
  }

  .come-row,
  .donate-row,
  .mobile-a-row {
    height: calc((100vh - 89px) * 0.12);
  }

  .center-col {
    border-right: none;
    overflow: hidden;
  }

  .what-is-it {
    font-size: 2rem;
  }
}

@media only screen and (max-width: 576px) {
  .what-is-it-row {
    height: calc((100vh - 89px) * 0.28);
  }

  .hair-row {
    height: calc((100vh - 89px) * 0.3);
  }

  .come-row,
  .donate-row,
  .mobile-a-row {
    height: calc((100vh - 89px) * 0.11);
  }

  .center-col {
    border-right: none;
    overflow: hidden;
  }

  .heart-button img {
    margin-right: 15%;
  }

  .heart-button-text, .what-is-it {
    margin-left: 15%;
  }

}
